import React from 'react';
import { createMarkFactoryMobile } from '@ali/4ever-plugin-marks';
import { moFactory } from '@ali/4ever-plugin-strike'

export default createMarkFactoryMobile({
  moFactory,
  type: 'italic',
  hotKey: 'mod+i',
  delimiter: [
    { character: '*', size: 1 },
    { character: '_', size: 1, disableIntraword: true },
  ],
  icon: 'italic',
});
